<template>
  <div>
    <aside class="menu" style="background: #CDCDCD">
      <p class="menu-label">
        Pages
      </p>
      <ul class="menu-list">
        <li v-for="page in config.pages">
          <a @click="loadPage(page)"><v-icon name="file"></v-icon> {{page.title}}</a>
        </li>
      </ul>
      <p class="menu-label">
        Settings
      </p>
      <ul class="menu-list">
        <li>
          <a @click="openConfig()"><v-icon name="settings"></v-icon> Configuration</a>
        </li>
      </ul>
    </aside>
    <b-modal :active.sync="isConfigModalOpened" has-modal-card :canCancel="true">
      <div class="card" style="padding: 10px; width: 500px">
        <h1 style="font-size: 18pt; font-weight: bold;">Configuration</h1>
        <vue-form-generator :schema="config.optionsSchema" :model="config.options"></vue-form-generator>
        <button class="button is-medium is-primary saveButton" @click="saveConfig">
            Save
        </button>
        <button class="button is-medium" @click="closeConfig">
            Close
        </button>
        <div style="clear: both;"></div>
      </div>
    </b-modal>

  </div>
</template>
<script>
import { mapState } from 'vuex';

var getGithubFile = function(store, path, callback) {
  return new Promise(function(resolve) {
    store.repository.getContents('gh-pages', path, false, function(err, contents) {
      if(err)
        console.error(err);

      callback(contents);
      resolve(contents);
    });
  });
};

export default {
  name: 'Menu',
  data() {
    return {
      isConfigModalOpened: false,
    };
  },

  methods: {
    loadPage(page, type) {
      this.$store.dispatch('changeEditedPage', page);
    },

    openConfig: function() {
      this.isConfigModalOpened = true;
    },

    saveConfig: function() {
      this.$store.repository.writeFile(
        'gh-pages', // e.g. 'gh-pages'
        'vue-git-cms.json', // e.g. 'blog/index.md'
        JSON.stringify(this.config, null, 2), // e.g. 'Hello world, this is my new content'
        'compile config', // e.g. 'Created new index'
        (err) => {
          if(!err) {
            this.isConfigModalOpened = false;
          }
        }
      );

      this.isConfigModalOpened = true;
    },

    closeConfig: function() {
      this.isConfigModalOpened = false;
    },

    loadConfig() {
      var _this = this;
      return getGithubFile(this.$store, 'vue-git-cms.json', (responseText) => {
        var config = JSON.parse(atob(responseText.content));
        _this.$store.dispatch('setConfig', config);
      });
    },
  },

  watch: {
    authenticated() {
      this.loadConfig();
    }
  },

  computed: mapState({
    authenticated: state => state.authenticated,
    config: state => state.config,
  })

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.menu {
}
</style>



