export function writeGitFile(store, filePath, content, message) {
  if(!message) message = 'edit ' + filePath;

  return store.repository.writeFile(
    'gh-pages',
    filePath,
    content,
    message,
    function(err) {}
  );
}

export function fragmentContent(store, filePath) {
  return store.state['fragment-' + filePath];
}

export function setFragmentContent(store, filePath, content) {
  store.state['fragment-' + filePath] = content;
}

export function getGithubFile(store, path, callback) {
  return new Promise(function(resolve) {
    store.repository.getContents('gh-pages', path, false, function(err, contents) {
      if(err)
        console.error(err);

      callback(contents);
      resolve(contents);
    });
  });
}



